.container {
    box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14),
        0px 3px 14px 2px rgba(0, 0, 0, 0.12);
    outline: 0;
    position: absolute;
    // max-width: calc(100% - 32px);
    min-width: 16px;
    // max-height: calc(100% - 32px);
    min-height: 16px;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 8px 0;
    background-color: #fff;
    font-family: 'Roboto', 'Roboto', 'Arial', sans-serif;
    border-radius: 4px;
    z-index: 100;
}

.positionContainer {
    position: absolute;
    // width: 100%;
    // height: 100%;
}
.background {
    top: 0;
    left: 0;
    // background-color: red;
    position: fixed;
    height: 100vh;
    width: 100vw;
    max-width: 100vw;
    max-height: 100vh;
    z-index: 50;
}
